.build-cart-summary{
    display: flex;
    flex-direction: column;
}

.build-cart-summary-header {
    display: flex;
    justify-content: space-around;
    color: white;
    background-color: blue;
    font-weight: bold;
    padding: 1em 0;
}

.build-cart-summary-footer{
    margin-bottom: 0;
    text-align: center;
}

.build-cart-summary-footer button{
    color: white;
    background-color: blue;
    font-weight: bold;
    width: 100%;
    border: 0;
    padding: 1em 0;
}