.payment-summary{
    margin-left: 10vw;
    width: 20vw;
    border-radius: 5px;
    box-shadow: -.5px .5px 5px 1px #888888, .5px .5px 5px 1px #d8d8d8
}

.payment-form{
    display: grid; 
    gap: 10px;
    padding: 1em;
    
}

.form-group{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-size: 1.3em;
    padding: .5em;
    background-color: white;
    border-radius: 5px;
    box-shadow: -.5px .5px 5px 1px #888888, .5px .5px 5px 1px #d8d8d8
}