.menu-item{
    display: grid;
    gap: 10px;
    padding: 1em;
    width: 50vw;
    border: 2px solid yellow;
    margin-bottom: 1em;
    border: 1px solid white;
    border-radius: 5px;
    box-shadow: -1px 1px 5px 1px #888888, 1px 1px 5px 1px #888888;
    background-color: white;
}

.menu-item input,select{
    display: block;
}

.item-header{
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    padding-bottom: 1em;
    border-bottom: 1px dashed black;
    background-color: white;
}

.menu-item-img{
    height: 100px;
    width: 130px;
}

.desc{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.item-middle{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 10px;
    padding: 1em 0;
    background-color: white;
}

.item-footer{
    display: grid;
    grid-template-columns: 2fr 1fr;
    align-items: center;
    text-align: center;
    background-color: white;
}

