*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  /* background-color: #f9f9f9; */
}

button{
  border-radius: 5px;
  padding: 7px;
  background-color: #1976d3;
  color: white;
  border: 0;
  font-size: .7em;
}

input{
  margin-top: 5px;
  padding: .2em;
  border: 0;
  border-radius: 5px;
  background-color: white;
  box-shadow: -.5px .5px 5px 1px #888888, .5px .5px 5px 1px #d8d8d8
}

label{
  font-size: .7em;
  font-weight: bold;
  background-color: white;
}