.event-detail{
    width: 45vw;
    margin-left: 200px;
    margin-bottom: 100px;
}

.event-detail-form{
    margin-top: 1em;
    background-color: white;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1em;
    border-radius: 5px;
    box-shadow: -1px 1px 5px 1px #888888, 1px 1px 5px 1px #888888;
    padding: 1.5em;
}

.event-detail-form-group{
    background-color: white;
}

.event-detail-form input{
    display: block;
    width: 100%;
}